import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import classNames from 'classnames';
import SnailLoader from '../common/SnailLoader';
import styleVariables from '../../lib/styleVariables.json';
import {CONCEAL_METHODS, REVEAL_METHODS} from '../../lib/constants';

class Home extends Component {

    componentDidMount() {
        console.log("MOUNTED HOME");
    }


    render() {
        let homeClass = classNames({
            'home-container': true
        });

        return (
            <div>
                <div className={homeClass}>
                    <div style={{flex: 1, justifyContent: 'center', alignItems: 'center'}}>
                        <SnailLoader />
                    </div>
                </div>

            </div>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

function mapDispatchToProps(dispatch) {
    const dispatchActions = bindActionCreators({}, dispatch);
    return {
        dispatch,
        actions: dispatchActions
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Home);


